*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --ffM:'Montserrat', sans-serif;
  --ffP: 'Poppins', sans-serif;
}
body{
  height: 100vh;
  background: #111;
}
.container{
  height: fit-content;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill , minmax(270px , 1fr));
  gap: .5em;
  flex-wrap: wrap;
  padding: .5em;

}
.card{
  height: 20rem;
  width: 100%;
  background: #131313;
  border-radius: 1em;

  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: .5em;
  padding: .5em;
}
.card:nth-child(1),
.card:nth-child(2),
.card:nth-child(3),
.card:nth-child(4){
  border: 1px solid transparent;
  border-bottom: 10px solid transparent;
  transition: 
  350ms cubic-bezier(0.075, 0.82, 0.165, 1) border,
  350ms cubic-bezier(0.075, 0.82, 0.165, 1) border-bottom;
}
.card:nth-child(1):hover{
  border: 1px solid #ff8859;
  border-bottom: 10px solid #ff8859; 
}
.card:nth-child(2):hover{
  border: 1px solid #fd6a7e;
  border-bottom: 10px solid #fd6a7e;
}
.card:nth-child(3):hover{
  border: 1px solid #fdcd60;
  border-bottom: 10px solid #fdcd60;
}
.card:nth-child(4):hover{
  border: 1px solid #7cccbd;
  border-bottom: 10px solid #7cccbd;
}

.img-con{
  height: 50px;
  aspect-ratio: 1;
}
.img-con img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.detail {
  color: rgb(197, 197, 197);
}
.detail h1{
font-family: var(--ffM);
width: 90%;
margin-bottom: .5em;
}
.detail p{
  font-family: var(--ffP);
  color: rgba(202, 202, 202, 0.815);
  display: block;
  display: -webkit-box;
  max-width: 98%;
  height: 71px;
  font-size: 16px;
  line-height: 1.1;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}